<template>
  <div class="op-authority-permission">
    <condition-frame class="op-query">
      <!--表单布局 主要是为了条件区域进行查询-->
        <el-form :model="query" :inline="true" ref="query" label-width="0">
          <el-form-item   prop="code">
                <el-input type="code" v-model="query.code"  placeholder="角色编码" auto-complete="off" class="sidebar-search"></el-input>
          </el-form-item>
           <el-form-item   prop="name">
                <el-input type="name" v-model="query.name"  placeholder="角色名称" auto-complete="off" class="sidebar-search"></el-input>
          </el-form-item>
          <el-form-item  prop="frontkey">
                <el-select v-model="query.frontkey" placeholder="请选择-网站端口" class="sidebar-search" clearable>
                    <el-option label="运营端" value="operate"></el-option>
                    <el-option label="城市端" value="city"></el-option>
                    <el-option label="商停集团版" value="pg"></el-option>
                    <el-option label="商停2.0" value="parking"></el-option>
                </el-select>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="fetchData" size="small">查询</el-button>
            <el-button @click="resetForm('query')"  size="small">重置</el-button>
            <el-button type="primary" size="small" @click = "increVisible = true">添加</el-button>
            <el-button type="primary" size="small" @click = "exportVisble = !exportVisble">导出</el-button>
          </el-form-item>
        </el-form>
    </condition-frame>
    <info-frame>
      当前总条数
      <op-badge>{{totalCount}}</op-badge>
      条
    </info-frame>
    <grid-frame>
      <template>
        <el-table
          :data="datas"
           border
          :height = "theight"
          ref="table"
          stripe
          v-loading.body="listLoading" element-loading-text="拼命加载中"  highlight-current-row fit cell-class-name = "h40"
          >
          <el-table-column
            prop="roleCode"
            label="角色编码"
            width="180">
          </el-table-column>
          <el-table-column
            prop="roleName"
            label="角色名称"
            width="280">
          </el-table-column>
          <el-table-column
            prop="description"
            label="权限描述"
            min-width="600">
          </el-table-column>
           <el-table-column
            prop="extendInfo"
            label="扩展信息"
            min-width="600">
          </el-table-column>
          <el-table-column
            label="是否内置"
            prop="isBuildIn">
            <template slot-scope="scope">
              {{scope.row.isBuiltIn | isBuiltInFilter}}
            </template>
          </el-table-column>
          <el-table-column
            prop="frontEndPortKey"
            label="前端口端">
          </el-table-column>
          <el-table-column fixed="right" label="操作" width="240">
                  <template slot-scope="scope">
                      <el-button type="text" size="small" @click = "isEditRole('roleForm', scope.row)">编辑</el-button>
                      <el-button type="text" size="small" @click = "setPermission(scope.row)">设置权限</el-button>
                      <el-button type="text" size="small" @click="removeUserRole(scope.row)">删除</el-button>
                  </template>
              </el-table-column>
        </el-table>
      </template>
      <pagination></pagination>
    </grid-frame>
      <exportExcel :exportHeader="exportHeader" :exportFilter="exportFilter" :getexportList="getexportList" :dialogVisble.sync="exportVisble" @getExportSize="getExportSize"></exportExcel>
      <!-- 保存角色 -->
      <el-dialog title="保存角色" :visible.sync="increVisible" @close = "isAddRole('roleForm')" width="42%" class="w800">
          <el-form :inline="true" ref="roleForm" :model="roleForm" :rules="roleRules" label-width="160px"  class="demo-form-inline">
              <el-form-item label="角色编码" prop="code">
                   <el-input v-model="roleForm.code" placeholder="请输入角色编号"></el-input>
              </el-form-item>
              <el-form-item label="角色名称" prop="name">
                  <el-input v-model="roleForm.name" placeholder="请输入角色名称"></el-input>
              </el-form-item>
               <el-form-item label="是否内置" prop="builtIn" >
                  <el-select v-model="roleForm.builtIn" placeholder="请选择">
                      <el-option label="是" value="1"></el-option>
                      <el-option label="否" value="0"></el-option>
                  </el-select>
              </el-form-item>
              <el-form-item label="网站端口" prop="frontkey">
                  <el-select v-model="roleForm.frontkey" placeholder="请选择">
                      <el-option label="运营端" value="operate"></el-option>
                      <el-option label="城市端" value="city"></el-option>
                      <el-option label="商停集团版" value="pg"></el-option>
                      <el-option label="商停2.0" value="parking"></el-option>
                  </el-select>
              </el-form-item>
              <el-form-item label="角色描述" prop="describe"  class='inline_textarea'>
                  <el-input type="textarea" v-model="roleForm.describe"></el-input>
              </el-form-item>
              <el-form-item label="扩展信息"  class='inline_textarea' prop="extendInfo">
                  <el-input type="textarea" v-model="roleForm.extendInfo"></el-input>
              </el-form-item>
              <el-form-item label="字符图标"  prop="image">
                <el-input type="text" v-model="roleForm.image"  placeholder="请输入" auto-complete="off"></el-input>
              </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
              <el-button @click="increVisible=false">取 消</el-button>
              <el-button type="primary" @click="addRole('roleForm')">确 定</el-button>
            </span>
      </el-dialog>
      <!-- 设置权限 -->
       <el-dialog title="设置角色权限" :visible.sync="roleVisible"  width="42%" class="w800" @close = "permissionDia">
          <el-form :inline="true" ref="permissionForm" :model="permissionForm"  label-width="160px"  class="demo-form-inline">
              <el-form-item label="角色" prop="roleName">
                 <el-input v-model="permissionForm.roleName"  type="size"  placeholder="请输入" disabled></el-input>                 
              </el-form-item>
              <br>
              <el-form-item label="选中缺省城区" prop="selectedRegions">
                  <el-cascader
                    ref = 'cascader'
                    v-model="permissionForm.selectedRegions"
                    :options="cityList"
                    @change="regionChange"
                    :props="regionProps"
                    change-on-select
                  ></el-cascader> 
                  <el-button type="text" @click = "addPermissionArea">添加城区</el-button>    
                  <el-button type="text" @click.once = "addAllArea" v-if="permissionForm.frontkey!== 'city'">添加全国</el-button>
              </el-form-item>
              <el-form-item  label="已选省市区" class="inline_textarea" v-if="regionData.length>0">
                   <el-table
                    :data="regionData"
                    border
                    clearable
                    cell-class-name = "h40"
                    style="width: 100%"
                    class="diaTable">
                    <el-table-column
                      prop="permissionName"
                      label="平台名称"
                    >
                    </el-table-column>
                    <el-table-column
                      prop="regionId"
                      label="城区ID"
                    >
                    </el-table-column>
                     <el-table-column
                      prop="regionName"
                      label="城区名称"
                    >
                    </el-table-column>
                    <el-table-column label="操作">
                      <template slot-scope="scope">
                        <el-button
                          size="mini"
                          type="danger"
                          @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                      </template>
                    </el-table-column>
                  </el-table>
              </el-form-item>
              <br>
              <el-form-item label="权限">
                  <el-tree
                    :props="perprops"
                    :data="treeData"
                    node-key="id"
                    ref="tree"
                    show-checkbox
                    highlight-current
                    @check-change="handleCheckChange"
                    @node-click="handleNodeClick"
                    :check-strictly="true"	
                    >
                  </el-tree>
              </el-form-item>
          </el-form>
          <span slot="footer" class="dialog-footer">
              <el-button @click="roleVisible=false">取 消</el-button>
              <el-button type="primary" @click="setRolePermission('permissionForm')">确 定</el-button>
            </span>
      </el-dialog>
    </div>
</template>

<script>
  import { mapGetters } from 'vuex'
  import { getRoleList, addRoles, removeRole, updateRole, getPermissionById, getAllPermission, setPermission } from '@/api/platform/rolemanage'
  import opBadge from 'components/base/opBadge.vue'
  import conditionFrame from 'components/base/conditionFrame.vue'
  import infoFrame from 'components/base/infoFrame.vue'
  import gridFrame from 'components/base/gridFrame.vue'
  import exportExcel from 'components/base/exportexcel/index'
  import pagination from 'components/base/pagination/index.vue'
  function isBuiltInFilter (type) {
    const builtInMap = {
      0: '否',
      1: '是'
    }
    return builtInMap[type]
  }
  export default {
    name: 'op-authority-permission',
    data () {
      return {
        increVisible: false,
        exportVisble: false,
        roleVisible: false,
        listLoading: true,
        isEdit: false,
        totalCount: 0,
        roleForm: {
          code: '',
          name: '',
          describe: '',
          builtIn: '',
          extendInfo: '',
          frontkey: '',
          image: '',
          id: ''
        },
        roleRules: {
          code: [
            {required: true, message: '请输入角色编号', trigger: 'blur'}
          ],
          name: [
            {required: true, message: '请输入角色名称', trigger: 'blur'}
          ],
          describe: [
            {required: true, message: '请输入权限描述', trigger: 'blur'}
          ],
          builtIn: [
            {required: true, message: '请选择是否内置', trigger: 'change'}
          ],
          image: [
            {required: true, message: '请输入字符编码', trigger: 'blur'}
          ]
        },
        permissionForm: {
          roleName: '',
          regionId: '',
          regionName: '',
          roleId: '',
          permissionName: '所有',
          permissionId: '',
          selectedRegions: [],
          frontkey: ''
        },
        query: {
          name: '',
          code: '',
          frontkey: ''
        },
        datas: [],
        regionData: [],
        exportHeader: [],
        exportFilter: [],
        regionProps: {
          label: 'name',
          value: 'id',
          children: 'list'
        },
        treeData: [],
        permissionList: [],
        perprops: {
          label: 'permissionName',
          children: 'subList'
        }
      }
    },
    created () {
      this.$store.dispatch('Get_CITYLIST')
      this.fetchData()
      this.getAllPermission()
    },
    mounted () {
    },
    computed: {
      ...mapGetters(['pageNum', 'pageSize', 'cityList']),
      theight () {
        return document.body.offsetHeight - 300
      }
    },
    watch: {
      pageSize () {
        this.fetchData()
      },
      pageNum () {
        this.fetchData()
      }
    },
    filters: {
      isBuiltInFilter
    },
    components: {conditionFrame, infoFrame, gridFrame, opBadge, pagination, exportExcel},
    methods: {
      /** ===================================获取角色列表========================================= */
      async fetchData (size) {
        this.listLoading = true
        this.listQuery = {
          roleCode: this.query.code,
          roleName: this.query.name,
          pageNo: this.pageNum,
          pageSize: this.pageSize,
          frontEndPortKey: this.query.frontkey
        }
        let res = await getRoleList(this.listQuery)
        if (res) {
          this.datas = res.returnObject.list
          this.totalCount = res.returnObject.totalCount
          this.$store.dispatch('SET_TOTALCOUNT', parseInt(this.totalCount))
        } else {
          this.datas = []
          this.totalCount = 0
          this.$store.dispatch('SET_TOTALCOUNT', 0)
        }
        this.listLoading = false
      },
      /** ===================================添加或编辑角色========================================= */
      // 编辑角色显示弹窗
      isEditRole (formName, row) {
        this.increVisible = true
        this.isEdit = true
        this.roleForm.code = row.roleCode
        this.roleForm.name = row.roleName
        this.roleForm.describe = row.description
        this.roleForm.extendInfo = row.extendInfo
        this.roleForm.frontkey = row.frontEndPortKey
        this.roleForm.builtIn = row.isBuiltIn + ''
        this.roleForm.image = row.roleImage
        this.roleForm.id = row.id
      },
      // 弹窗关闭回调
      isAddRole (formName) {
        if (this.isEdit) {
          this.isEdit = false
          this.roleForm = {
            code: '',
            name: '',
            describe: '',
            builtIn: '',
            extendInfo: '',
            frontkey: '',
            image: ''
          }
        }
      },
      async addRole (formName) {
        let valid = await this.$refs[formName].validate()
        if (valid) {
          this.listQuery = {
            roleCode: this.roleForm.code,
            roleName: this.roleForm.name,
            description: this.roleForm.describe,
            extendInfo: this.roleForm.extendInfo,
            frontEndPortKey: this.roleForm.frontkey,
            isBuildIn: this.roleForm.builtIn,
            parentId: '0',
            roleImage: this.roleForm.image
          }
          let res
          if (this.isEdit) {
            this.$set(this.listQuery, 'roleId', this.roleForm.id)
            res = await updateRole(this.listQuery)
          } else {
            res = await addRoles(this.listQuery)
          }
          if (res) {
            this.increVisible = false
            this.$message({
              type: 'success',
              message: res.message
            })
            this.$refs[formName].resetFields()
            this.fetchData()
          }
        } else {
          return false
        }
      },
      /** ================================= 删除人员================================= */
      async removeUserRole (row) {
        let flag = await this.$confirm('是否永久删除该用户?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        if (flag) {
          let res = await removeRole(row.id)
          if (res) {
            this.$message({
              type: 'success',
              message: '删除成功!'
            })
            this.fetchData()
          }
        } else {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        }
      },
      // 重置查询表单
      resetForm (formName) {
        this.$store.dispatch('SET_PAGESIZE', 15)
        this.$store.dispatch('SET_PAGENUM', 1)
        this.$refs[formName].resetFields()
        this.fetchData()
      },
      /** ===========================导出================================ **/
      getExportSize () {
        const tableData = this.$refs.table.$slots.default
        tableData.forEach((ele) => {
          if (ele.componentOptions && ele.componentOptions.propsData.prop) {
            let propData = ele.componentOptions.propsData
            this.exportHeader.push(propData.label)
            this.exportFilter.push(propData.prop)
          }
        })
      },
      getexportList (size) {
        let listQuery = {
          roleCode: this.query.code,
          roleName: this.query.name,
          pageNo: this.pageNum,
          pageSize: size,
          frontEndPortKey: this.query.frontkey
        }
        return new Promise((resolve, reject) => {
          getRoleList(listQuery).then((res) => {
            let list = res.returnObject.list
            list.map(v => {
              v.isBuiltIn = isBuiltInFilter(v.isBuiltIn)
            })
            resolve(list)
          }, err => reject(err))
        })
      },
      /** ===========================设置权限==============================**/
      setPermission (row) {
        this.roleVisible = true
        this.permissionForm.roleName = row.roleName
        this.permissionForm.roleId = row.id
        this.permissionForm.frontkey = row.frontEndPortKey
        this.getPermission(row.id)
      },
      // 根据角色Id获取权限
      async getPermission (roleId) {
        const res = await getPermissionById(roleId)
        // const list = this.metaData(res.returnObject, true)
        const list = res.returnObject
        if (list && list.length) {
          this.$refs.tree.setCheckedNodes(list)
          this.addCheckedRegion(this.treeData, list)
        }
        // 设置已选中的权限
      },
      // 递归添加已选城区
      addCheckedRegion (treeData, list) {
        treeData.map(v => {
          list.map(t => {
            if (v.id === t.id) {
              v['regionName'] = t.regionName
              v['scopeObjectId'] = t.scopeObjectId
            }
            if (v.subList.length > 0) {
              this.addCheckedRegion(v.subList, list)
            }
          })
        })
      },
      // 获取所有权限
      async getAllPermission () {
        const res = await getAllPermission()
        const list = res.returnObject
        const treeData = this.metaData(list)
        this.treeData = treeData
      },
      // 处理权限数据
      metaData (list, getCheckedNodes) {
        let newList = []
        if (!list.length) return false
        list.sort((a, b) => { return a.displayOrder - b.displayOrder })
        list.forEach(item => {
          const id = item.id
          const pid = item.parentId
          item['subList'] = []
          list.map(v => {
            const _id = v.id
            const _pid = v.parentId
            if (id === _pid) {
              item.subList.push(v)
            }
            if (pid === _id) {
              this.$set(v, 'hasChild', true)
            }
          })
          if ((pid === '' || pid === '0') && !getCheckedNodes) {
            newList.push(item)
          } else if (getCheckedNodes && item.subList.length === 0) {
            newList.push(item)
          }
        })
        return newList
      },
      // 添加全国
      addAllArea () {
        if (this.permissionForm.frontkey === 'operate' || this.permissionForm.frontkey === 'pg') {
          let permissionName = this.permissionForm.permissionName
          if (permissionName === '') {
            permissionName = '所有'
          }
          this.regionData.forEach((v, i) => {
            if (v.permissionId === '') {
              this.regionData.splice(i, 1)
            }
          })
          this.regionData.unshift({
            permissionName: permissionName,
            permissionId: '',
            regionId: '000000',
            regionName: '全国'
          })
          this.rangeCheck(this.permissionForm.permissionId, this.treeData, '全国', '000000')
        }
      },
      // 添加权限城区
      addPermissionArea (flag) {
        if (this.permissionForm.frontkey === 'city' && this.permissionForm.selectedRegions.length < 2) {
          this.$message({
            type: 'warning',
            message: '城市端角色必须选择到市区'
          })
          return false
        }
        if ((this.permissionForm.frontkey === 'operate' || this.permissionForm.frontkey === 'pg') && this.permissionForm.selectedRegions.length === 3) {
          this.$message({
            type: 'warning',
            message: '运营端角色不能选择到区'
          })
          return false
        }
        if (this.permissionForm.regionId !== '') {
          const permissionArea = {
            permissionName: this.permissionForm.permissionName,
            permissionId: this.permissionForm.permissionId,
            regionId: this.permissionForm.regionId,
            regionName: this.permissionForm.regionName
          }
          if (this.regionData.length < 1 || (this.regionData[0].permissionId === '' && this.regionData.length < 2)) {
            this.regionData.push(permissionArea)
            this.rangeCheck(this.permissionForm.permissionId, this.treeData, this.permissionForm.regionName, this.permissionForm.regionId)
          } else if (this.regionData[0].permissionId !== '' || this.regionData.length === 2) {
            this.regionData.splice(-1, 1, permissionArea)
            this.rangeCheck(this.permissionForm.permissionId, this.treeData, this.permissionForm.regionName, this.permissionForm.regionId)
          }
        } else {
          return false
        }
      },
      // 城区选择
      regionChange (val) {
        if (this.permissionForm.frontkey === 'city' && val.length < 2) return
        if ((this.permissionForm.frontkey === 'operate' || this.permissionForm.frontkey === 'pg') && val.length === 3) return
        // this.permissionForm.regionName = this.$refs.cascader.currentLabels.join('/')
        this.permissionForm.regionName = this.$refs.cascader.currentLabels.slice(-1)[0]
        this.permissionForm.regionId = val.slice(-1)[0]
      },
      // 递归查询
      rangeCheck (permissionId, list, regionName, regionId) {
        list.forEach(item => {
          if (item.id === permissionId || item.parentId === permissionId || permissionId === '') {
            item['regionName'] = regionName
            item['scopeObjectId'] = regionId
          } else {
            this.rangeCheck(permissionId, item.subList, regionName, regionId)
          }
          if ((item.id === permissionId || permissionId === '' || item.parentId === permissionId)) {
            this.rangeCheck(item.id, item.subList, regionName, regionId)
          }
        })
      },
      // 删除城区
      handleDelete (index, row) {
        let regionId
        let regionName
        this.regionData.forEach((item, key) => {
          if (row.regionId === item.regionId) {
            this.regionData.splice(key, 1)
          }
          if (item.permissionId === '') {
            regionId = item.regionId
            regionName = item.regionName
          }
          // 设置默认城区
          if (item.regionId && item.permissionId === '') {
            this.rangeCheck(item.permissionId, this.treeData, item.regionName, item.regionId)
            return false
          } else {
            this.rangeCheck(item.permissionId, this.treeData, regionName, regionId)
          }
        })
      },
      handleCheckChange (data, checked, indeterminate) {
        console.log(data, checked, indeterminate)
      },
      // 节点点击回调
      handleNodeClick (data) {
        this.permissionForm.permissionName = data.permissionName
        this.permissionForm.permissionId = data.id
        this.permissionForm.regionId = data.scopeObjectId || ''
        this.permissionForm.regionName = data.regionName || ''
        debugger
        this.addPermissionArea(true)
      },
      // 权限弹窗关闭回调
      permissionDia () {
        this.regionData = []
        this.permissionForm.permissionName = '所有'
        this.permissionForm.frontkey = ''
        this.$refs.tree.setCheckedNodes([])
        this.permissionList = []
        this.$refs['permissionForm'].resetFields('selectedRegions')
      },
      // 保存角色权限
      async setRolePermission () {
        let nodes = this.$refs.tree.getCheckedNodes()
        if (this.treeData.length > 0) {
          nodes.map(v => {
            this.permissionList.push({
              permissionId: v.id,
              scopeObjectId: v.scopeObjectId
            })
          })
        }
        let query = {
          roleId: this.permissionForm.roleId,
          rolePermissionJson: JSON.stringify(this.permissionList)
        }
        const res = await setPermission(query)
        this.$message({
          type: 'success',
          message: res.message
        })
        this.permissionList = []
        // console.log(res)
      }
    }
  }
</script>

<style lang="less">
  .op-authority-permission {
    width: 100%;
    height: 100%;
    .op-query{
      padding: 10px 0;
      border-bottom: 1px dashed #d4d4d4;
      margin-bottom: 10px;
    }
    .inline_textarea{
      width: 100%;
      .el-form-item__content{
        width: 76%;
      }
    }
  }
</style>



