import fetch from '@/utils/fetch'
// 获取角色列表
export function getRoleList ({roleCode, roleName, pageNo, pageSize, frontEndPortKey}) {
  return fetch({
    url: '/role/getRolesByPager',
    method: 'get',
    params: {
      roleCode,
      roleName,
      pageNo,
      pageSize,
      frontEndPortKey
    }
  })
}
// 添加角色
export function addRoles ({roleCode, roleName, description, extendInfo, frontEndPortKey, isBuildIn, parentId, roleImage}) {
  return fetch({
    url: '/role/addRole',
    method: 'post',
    data: {
      roleCode,
      roleName,
      description,
      extendInfo,
      frontEndPortKey,
      isBuildIn,
      parentId,
      roleImage
    }
  })
}
// 编辑角色
export function updateRole ({roleCode, roleName, roleId, description, extendInfo, frontEndPortKey, isBuildIn, parentId, roleImage}) {
  return fetch({
    url: '/role/updateRole',
    method: 'post',
    data: {
      roleCode,
      roleName,
      roleId,
      description,
      extendInfo,
      frontEndPortKey,
      isBuildIn,
      parentId,
      roleImage
    }
  })
}
// 删除角色
export function removeRole (roleId) {
  return fetch({
    url: '/role/removeRole',
    method: 'post',
    data: {roleId}
  })
}
// 根据角色Id获取权限
export function getPermissionById (roleId) {
  return fetch({
    url: '/role/getPermissionById',
    method: 'get',
    params: {roleId}
  })
}

// 获取所有权限列表
export function getAllPermission () {
  return fetch({
    url: '/role/getAllPermission',
    method: 'get'
  })
}
// 删除角色权限
export function clearRolePermission (roleId) {
  return fetch({
    url: '/role/clearRolePermission',
    method: 'post',
    data: {
      roleId
    }
  })
}

// 设置角色权限
export function setPermission ({roleId, rolePermissionJson}) {
  return fetch({
    url: '/role/setPermission',
    method: 'post',
    data: {
      roleId,
      rolePermissionJson
    }
  })
}



