<template>
  <div class="doexport">
    <el-dialog title="导出选项" :visible.sync="exportVisible" width="30%">
      <template>
        <el-form :model="exportForm" ref="exportForm" label-width="100px" class="demo-ruleForm">
          <el-form-item label="导出条数" prop="size" :rules="rules">
            <el-input type="size"  v-model.number="exportForm.size" auto-complete="off" @keyup.enter="submitForm('exportForm')" ></el-input>
          </el-form-item>
        </el-form>
      </template>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="submitForm('exportForm')">确认导出</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  name: 'doexport',
  data () {
    return {
      exportVisible: false,
      downloadLoading: false,
      exportForm: {
        size: 0
      },
      exportList: [],
      rules: [
        { required: true, message: '导出数不能为空' },
        { type: 'number', message: '导出条数必须为数字值' }
      ]
    }
  },
  props: {
    getexportList: {
      type: Function,
      default: () => { return [] }
    },
    exportHeader: {
      type: Array,
      default: []
    },
    exportFilter: {
      type: Array,
      default: []
    },
    dialogVisble: {
      type: Boolean,
      default: false
    }
  },
  watch: {
    dialogVisble () {
      this.exportVisible = !this.exportVisible
    }
  },
  methods: {
    handleDownload () {
      this.downloadLoading = true
      require.ensure([], () => {
        const { export_json_to_excel } = require('./lib/Export2Excel')
        const tHeader = this.exportHeader
        const filterVal = this.exportFilter
        const list = this.exportList
        const data = this.formatJson(filterVal, list)
        export_json_to_excel(tHeader, data, '列表excel')
        this.downloadLoading = false
      })
    },
    formatJson (filterVal, jsonData) {
      return jsonData.map(v => filterVal.map(j => v[j]))
    },
    async submitForm (formName) {
      let valid = await this.$refs[formName].validate()
      if (valid) {
        this.exportVisible = false
        this.getExportSize()
        let list = await this.getexportList(this.exportForm.size)
        this.exportList = list
        this.handleDownload()
      } else {
        return false
      }
    },
    getExportSize () {
      this.$emit('getExportSize')
    }
  }
}
</script>




