<template>
  <div class="op-content-condition-frame">
    <slot></slot>
  </div>
</template>

<script>
  export default {
    name: 'op-content-condition-frame',
    props: {
      height: {
        type: Number,
        default: 30
      }
    }
  }
</script>

<style lang="less">
  .op-content-condition-frame {
    padding: 10px 0;
  }
</style>



